import React from "react"

const SimCard = ({ sim }) => {
    return (
        <>
            <h2>SIM Card</h2>
            <p>FCCID: {sim.fccId}</p>
            <p>  : {sim.simNumber}</p>
            <p> : {new Date(sim.mountingDate).toLocaleDateString()}</p>
            <p> : {sim.pultNumber}</p>
            <p> : {sim.mountingPlace}</p>
            <p> : {sim.mountingAddress}</p>
        </>
    )
}

export default SimCard